.App {
  text-align: center;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.rangePanel {
  display: flex;
  justify-content: center;
}

.separator {
  margin-left: 200px;
}

.fieldSpacer {
  width: 62px;
}

.rangePickerPanel {
  display: flex;
  justify-content: center;
  padding-top: 50px;
}

.spacer {
  height: 50px;
}

.dateFieldPanel {
  padding-bottom: 30px;
  display: flex;
}
.timeFieldPanel {
  display: flex;
  justify-content: space-between;
}

.fieldPanel {
  display: flex;
  flex-direction: column;
  padding-bottom: 30px;
}

.css-1biko1i {
  margin-left: 5px !important;
  margin-right: 5px !important;
  color: white;
}
